.main-content {
    margin: 0px;
    padding: 10px ;
  }
.main-layout {
    padding: 0px;
    margin: 0px;
    min-height: 100vh !important;
    
}
.main-layout.ant-layout  
.ant-menu{
  background-color: transparent !important;
}
.config-container,
.container {
    margin: 0 auto;
    max-width: 1280px;
    //background-color: aquamarine;
}
.config-container{
    width: 100%;
    transform: translateY(-150px);
}
.container{
    width: 90%;
}
@media only screen and (min-width: 601px) {
    .container {
        width: 85%;
    }
    .config-container{
        width: 85%;
    }
}
@media only screen and (min-width: 993px) {
    .container {
        width: 70%;
    }
    .config-container{
        width: 70%;
    }
}

.config-title{
    font-weight: bold;
    font-size: 28px;
    padding: 0 10px;
    color: var(--palette-white);
}
Button, input{
    border-radius: 50px !important;
}
.ant-btn .btn,
Button.btn{
    line-height: 0.5 !important;
    padding: 10px 30px !important;
}
input{
    margin: 10px 0 !important;
}

.card-button,
button.card-button{
    margin: 10px;
    border-radius: 10px !important;
}
button.card-button{
    height:150px !important;
    width: 150px !important;
}
div.card-button{
    display: block;
    float: left;
    width: 200px !important;
}
.card-button > .card-container{
    padding: 10px;
}
.card-button > .card-container .card-title{
    text-align: center;
    font-size: 1.5em;
    padding: 10px 5px 0 5px;
}
.card-button > .anticon,
.card-button > .card-container .anticon {
    width: 50px;
    height: 50px;
    display: block !important;
    margin: 0 auto;
    padding: 10px;
    border-radius: 50px;
    font-size: 2em !important;
}
.card-button > span {
    margin: 10px 0;
}
.card-button > .card-footer,
.card-login > .card-footer{
    min-height: 50px;
    width: 100%;
    display: block;
    padding: 10px;
}
.card-footer > .footer-row{
    width: 100%;
    display: block;
    padding: 2px;
}
.card-footer > .footer-row .anticon {
    margin: 0 5px;
    padding: 5px;
    border-radius: 50px;
    font-size: 1em !important;
}

.card-login {
    height: auto;
    max-width: 300px;
    margin: calc(100px) auto ;
    border-radius: 10px;
}
.card-database > .card-container,
.card-login > .card-container{
    margin: 10px 30px
}
.card-login > .card-container img{
    height: auto;
    width: 80px;
    margin: 15px auto;
    display: block;
}

.card-login > .card-container Button{
    width: 100%;
    margin-top: 40px;
    margin-bottom: 10px;
} 

.card-login > .card-footer  Button {
    display: block;
    margin: 10px auto;
    height: 70px;
    width: 70px;
}
.card-login > .card-footer  Button .anticon{
    font-size: 2em !important;
}

.card-database {
    border-radius: 10px;
}
.card-database .card-container  .card-info{
    padding: 20px;
    margin: 20px 0px;
    border-radius: 5px;
}
.card-database > .card-header{
    padding: 20px 40px;
}
.chip{
    margin: 0 10px;
}
.chip i.anticon {
    height: 25px;
    width: 25px;
    padding: 5px;
    border-radius: 50px;
    transform: translateX(-12px);
    color: var(--palette-white);
}

.chip.ant-btn > .anticon + span,
.chip.ant-btn > span + .anticon{
    margin-left: 0px;
}
/* Collapse style standar */
.ant-collapse.collapse-collections {
    background-color: transparent;
    border: 0;
}
.ant-collapse.collapse-collections >
.ant-collapse-item{
    margin-bottom: 10px;
    border: 0px ;
}
.ant-collapse.collapse-collections >
.ant-collapse-item  .ant-collapse-header{
    height: var(--ant-collapse-header);
    border-radius: 5px !important; 
    padding: 12px 0px 12px 60px !important;
    border: 1px solid var(--palette-gray-1);
}
.ant-collapse.collapse-collections >
.ant-collapse-item.ant-collapse-item-active .ant-collapse-header{
    border-radius: 5px 5px 0 0 !important;
}
.ant-collapse-item.ant-collapse-item-active .ant-collapse-header 
.pulgar-collapse{
    border-radius: 0 4px 0 0;
}
.ant-collapse.collapse-collections > .ant-collapse-item 
.ant-collapse-header .anticon.icon-collapse{
    height: 30px;
    width: 30px;
    top:calc(50% - 15px);
    left: 15px;
    position: absolute;
    padding: 6px;
    font-size: 18px;
    border-radius: 50px;
}

.ant-collapse.collapse-collections > .ant-collapse-item 
.ant-collapse-header .segurity-collapse{
    top: 0px;
    position: absolute;
    height: calc(100% );
    padding: 0 5px;
}
.ant-collapse.collapse-collections > .ant-collapse-item 
.ant-collapse-header .segurity-collapse .anticon{
    height: 50px;
    width: 30px;
    color: var(--palette-white);
    padding-top: 22px;
    font-size: 18px;
    border-radius: 0 0  50px 50px;
}
.ant-collapse.collapse-collections > .ant-collapse-item 
.ant-collapse-header .title-collapse{
    top: 0px;
    padding: 2px 0 0 0;
    display: grid;
    margin-left: 40px;
    position: absolute;
    height: calc(100%);
}
.ant-collapse-header .title-collapse strong{
    padding: 0 5px;
    font-size: 1.4em;
    line-height: 1.4;

}
.ant-collapse-header .title-collapse small{
    padding: 0 5px;
    line-height: 1;
}
.ant-collapse-header .title-collapse > span > i{
    margin: 0 10px; 
}
.ant-collapse.collapse-collections > .ant-collapse-item 
.ant-collapse-header .option-collapse{
    height: 40px;
    width: 220px;
    margin-top:10px;
}
.ant-collapse.collapse-collections  .ant-collapse-item .ant-collapse-content {
    border-top: 0px;
    background-color: var(--palette-gray-darken-4);
}
.pulgar-collapse{
    height: 100%;
    width: 60px;
    top: 0;
    right: 0;
    position: absolute;
    border-radius: 0 4px 4px 0;
}
.pulgar-collapse .anticon {
    position: absolute;
    top: calc(50% - 15px) !important;
    left: calc(50% - 20px) !important;
}
.ant-collapse.collapse-collections > .ant-collapse-item 
.ant-collapse-header .option-collapse .anticon{
    height: 30px;
    width: 30px;
    padding: 6px;
    font-size: 18px;
    border-radius: 50px;
    margin: 3px;
}
.ant-collapse.collapse-collections > 
.ant-collapse-item.ant-collapse-item-active .anticon.anticon-right > svg {
    transform: rotate(90deg);
}
.breadcrumb{
    font-size: 18px;
}
/* Theme Light */
