.page-404{
    height: auto;
    width: 100%;
    display: block;
    margin-top: 100px;
    text-align: center;
}
.page-404 h1{
    font-size: 2.2em;
    font-weight: bold;
    margin-bottom: 4px;
    
}
.page-404 h3{
    font-size: 1.5em;
    
}
.page-404 img{
    height: auto;
    width: 100%;
}
.page-404 img{
    height: auto;
    width: 100%;
}

.main-layout.light.ant-layout .page-404 h1,
.main-layout.light.ant-layout .page-404 h2{
    color: var(--palette-yellow);
}
.main-layout.light.ant-layout .page-404 h3,
.main-layout.light.ant-layout .page-404 h4{
    color: var(--palette-orange);
}

.main-layout.dark.ant-layout .page-404 h1,
.main-layout.dark.ant-layout .page-404 h2{
    color: var(--palette-purple);
}
.main-layout.dark.ant-layout .page-404 h3,
.main-layout.dark.ant-layout .page-404 h4{
    color: var(--palette-orange);
}